.cartItem {
  width: 100%;
  height: 7rem;
  display: flex;
  position: relative;
  align-items: center;
  text-align: center;
  margin-top: 5px;
}
.cartItemImage {
  height: 100%;
  width: 30%;
  border: 1px solid rgb(158, 158, 158);
}
.cartItemImage img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}
.cartItemDescription {
  height: 100%;
  width: 20%;
  font-size: 0.8rem;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}
.cartItemPrice {
  height: 100%;
  width: 30%;
  display: flex;
  position: absolute;
  right: 0;
  justify-content: center;
  align-items: center;
}
.cartRemove{
  background-color: #d2042d;
  padding: 10px;
  width: 100%;
  margin-top: 5px;
  color: white;
  border: 1px solid rgb(202, 202, 202);
}
.cartRemove:hover {
  background-color: white;
  color: #d2042d;
  cursor: pointer;
  border: 1px solid #d2042d;
}

