.shop {
  background-image: url("../images/pattern4.png");
}
.shopFront {
  width: 100%;
  height: 80vh;
  position: relative;
  display: flex;
  background-color: #000000cc;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 1);
}
.shopFrontDiv {
  width: 40%;
  margin: auto;
  text-align: center;
  display: flex;
  align-items: center;
  color: #d2042d;
}
.shopFront img {
  margin: auto;
  width: 50%;
}
.shopFrontDiv div {
  margin: auto;
}
.shopFrontDiv h1 {
  font-size: 7rem;
}
.shopFrontDiv p {
  font-family: "Roboto";
  font-size: 1.4rem;
}
.shopFrontDiv button {
  font-size: 1.5rem;
  padding: 10px;
  color: #d2042d;
  transition: 0.3s;
  border: 1px solid #d2042d;
  background-color: rgba(255, 0, 0, 0);
}
.shopFrontDiv button:hover {
  cursor: pointer;
  background-color: #d2042d;
  color: white;
  border: 1px solid white;
  transition: 0.3s;
  transform: scale(1.1);
}

.shopAd {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  height: 50vh;
}
.shopAd img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  /* height: auto; */
}

.storeContainer {
  width: 95%;

  margin: 20px auto;
  display: flex;

  background-color: rgba(255, 255, 255);
}
.storeCheckbox {
  width: 10%;
  height: auto;
  background-color: rgb(221, 221, 221);
  text-align: center;
  display: flex;
  justify-content: center;
}
.storeCheckboxInner {
  margin-top: 20px;
}
.storeCheckboxInner span {
  margin-left: 10px;
  font-family: "Roboto";
}
.storeCheckboxInner input {
  appearance: none;
  -webkit-appearance: none;
  border-radius: 50%;
  border: 10px solid #fff;
  box-shadow: 0 0 0 1px #D2042D;
}
.storeCheckboxInner input:checked {
  border: 10px solid #e98498; 
}
.storeCheckbox label {
  display: flex;
  align-items: center;
}
.store {
  width: 85%;
  height: 100%;
}
.store::-webkit-scrollbar {
  display: none;
}
